import './style.css'
import gsap from 'gsap'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js'
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js'

const scene = new THREE.Scene()

const fontLoader = new FontLoader()
const textureLoader = new THREE.TextureLoader()
const TextureImg = textureLoader.load('bg.png')
scene.background = TextureImg;

fontLoader.load(
    '/fonts/helvetiker_regular.typeface.json',
    (font) =>
    {
        const textGeometry = new TextGeometry(
            ' G7M7T1\nDeveloper\n Designer',
            {
                font,
                size: 0.5,
                height: 0.2,
                curveSegments: 5,
                bevelEnabled: true,
                bevelThickness: 0.03,
                bevelSize: 0.02,
                bevelOffset: 0,
                bevelSegments: 4
            }
        )

        textGeometry.center()
        // MeshMatcapMaterial({ matcap: matcapTexture })
        const textMaterial = new THREE.MeshNormalMaterial()
        const text = new THREE.Mesh(textGeometry, textMaterial)
        scene.add(text)

        const donutGeometry = new THREE.TorusGeometry(0.3,0.2,20,45)
        const boxGeometry = new THREE.BoxGeometry(1,1,1)
        const donutMaterial = new THREE.MeshNormalMaterial()

        function checker(numbers) {
            if (numbers > 0) {
                return numbers + 1
            } else {
                return numbers - 1
            }
        }

        for (let i = 0; i < 100; i++)
        {
            const donut = new THREE.Mesh(donutGeometry, donutMaterial)
            const boxs = new THREE.Mesh(boxGeometry, donutMaterial)
            donut.position.x = checker((Math.random() - 0.5) * 10)
            donut.position.y = checker((Math.random() - 0.5) * 10)
            donut.position.z = checker((Math.random() - 0.5) * 10)

            boxs.position.x = checker((Math.random() - 0.5) * 35)
            boxs.position.y = checker((Math.random() - 0.5) * 35)
            boxs.position.z = checker((Math.random() - 0.5) * 35)

            donut.rotation.x = (Math.random() * Math.PI)
            donut.rotation.y = (Math.random() * Math.PI)
            donut.rotation.z = (Math.random() * Math.PI)

            boxs.rotation.x = (Math.random() * Math.PI)
            boxs.rotation.y = (Math.random() * Math.PI)
            boxs.rotation.z = (Math.random() * Math.PI)
            scene.add(donut, boxs)
        }
    }
)

// const AmbientLight = new THREE.AmbientLight(0x404040)
// scene.add(AmbientLight)
//
// const PointLight = new THREE.PointLight(0xff0000,1,100)
// PointLight.position.set(0,1,2)

const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 1000)
scene.add(camera)
camera.position.z = 3
// camera.position.y = 1
const tl = gsap.timeline({repeat: -1, repeatDelay: 1, ease: "Linear"});
tl.to(camera.position, { duration: 2, delay: 0, x: 3 })
tl.to(camera.position, { duration: 4, delay: 0, x: -3})
tl.to(camera.position, { duration: 4, delay: 0, x: 0})

const canvas = document.querySelector("canvas.webgl")
const renderer = new THREE.WebGLRenderer({
    canvas
})
renderer.setSize(sizes.width,sizes.height)

// controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

window.addEventListener('resize', () =>
{
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

window.addEventListener('dblclick', () =>
{
    const fullscreenElement = document.fullscreenElement || document.webkitFullscreenElement

    if(!fullscreenElement)
    {
        if(canvas.requestFullscreen)
        {
            canvas.requestFullscreen()
        }
        else if(canvas.webkitRequestFullscreen)
        {
            canvas.webkitRequestFullscreen()
        }
    }
    else
    {
        if(document.exitFullscreen)
        {
            document.exitFullscreen()
        }
        else if(document.webkitExitFullscreen)
        {
            document.webkitExitFullscreen()
        }
    }
})

const clock = new THREE.Clock()

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    controls.update()
    renderer.render(scene, camera)
    window.requestAnimationFrame(tick)
}
tick()
